<template>
  <el-card
    :body-style="{ padding: '7px' }"
    v-on:click="click_button"
    class="ti_element"
  >
    <el-image
      :src="img"
      :fit="'cover'"
      class="element_image"
    ></el-image>
    <div :class="active_class">{{ text_var }}</div>
    <Down_arrow :class="arrow_button" />
  </el-card>
</template>

<script>
import Down_arrow from "@/components/double_arrow.vue";
export default {
  data() {
    return {
      text_var: this.label,
      is_close: true,
      arrow_button: "no-rotate-arrow",
      active_class: "element_text_header",
    };
  },
  props: {
    label: String,
    text: String,
    img: String,
  },
  components: {
    Down_arrow,
  },
  methods: {
    click_button: function () {
      if (this.is_close) {
        this.text_var = this.text;
        this.is_close = false;
        this.arrow_button = "rotate-arrow";
        this.active_class = "element_text";
        //pass
      } else {
        this.text_var = this.label;
        this.is_close = true;
        this.arrow_button = "no-rotate-arrow";
        this.active_class = "element_text_header";
      }
    },
  },
};
</script>

<style lang="scss">
.ti_element {
  .element_text {
    text-align: left;
    padding-left: 10px;
    padding-right: 10px;
    font-size: 15px;
    line-height: 17px;
    font-family: "Comfortaa", cursive;
    margin-bottom: 10px;
    margin-top: 10px;
    // 1px solid var(--el-border-color-base);
  }

  .element_text_header {
    text-align: left;
    padding-left: 10px;
    padding-right: 10px;
    font-size: 25px;
    line-height: 17px;
    font-family: "Comfortaa", cursive;
    margin-bottom: 2px;
    margin-top: 15px;
    // 1px solid var(--el-border-color-base);
  }

  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 10px 0;
}

.rotate-arrow {
  transform: rotate(180deg);
  border-top: 0px;
}

.no-rotate-arrow {
  transform: rotate(0deg);
  border-top: 0px;
}
</style>
